@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

:root {
  --blue: #09293D;
  --white: #F8F9FA;
  --green: #63D779;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 200;
}
.loginContainer{
  width: 100vw;
  background-color: var(--blue);
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.loginContainer label,input{
  padding: 1vmin;
  font-size: 3vmin;
  border-radius: 5px;
  border: none;
  font-weight: 300;

}

input[type=submit]{
  background-color:var(--blue);
  padding: 1vmin 3vmin;
  margin: 2vmin;
  color: #F8F9FA;
  font-size: 4vmin;
  border-radius: 20px;
}

.login{
 background-color: rgb(21, 145, 147);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  height: 60%;
  font-size: 5vmin;
  border-radius: 20px;
  box-shadow: 2px 2px 30px rgb(82, 104, 104) ;

}
.login .title{
 
 left: 0px;
 margin-bottom: 6vmin;
}
.container {
  display: grid;
  height: 100vh;
  grid-template-areas:
    "sidebar topbar topbar"
    "sidebar content1 content2";
  grid-template-columns: 1.3fr 2fr 2fr;
  gap: 0px;
  grid-template-rows: 20%;
  background-color: var(--white);
  color: #F8F9FA;
  font-size: 5vmin;
  padding: 1vmin;
  box-sizing: border-box;
  

}

.topbar {
  grid-area: topbar;
  margin: 1vmin;
  border-radius: 1vmin;
  padding: 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.699);
  background-color: var(--green);
}

.sidebar {
  grid-area: sidebar;
  margin: 1vmin;
  border-radius: 1vmin;
  padding: 1px;
  background-color: rgb(21, 145, 147);
  box-shadow: 0 0px 20px rgba(0, 0, 0, 0.699);

  /* background-image: linear-gradient(to bottom , var(--blue), var(--green)); */

}

.content1 {
  grid-area: content1;
  margin: 1vmin;
  border-radius: 1vmin;
  padding: 1px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.699);
  background-color: var(--green);

  
}

.content2 {
  grid-area: content2;
  margin: 1vmin;
  border-radius: 1vmin;
  padding: 1px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.699);
  background-color: var(--green);
}

.title {

  position: relative;
  top: 30px;
  left: 30px;
  font-size: 5vmin;
  font-weight: 300;

}

button {
  width: 20vw;

  margin: 1vmin;
  color: #F8F9FA;
  background-color: var(--blue);
  border-radius: 5px;
  border: none;
  font-size: 4vmin;
  padding: 1vmin;
  
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.699);
 transition: all 0.5s ;
}
button:hover{
  background-color:rgba(195, 243, 207, 0.92);
  color: #09293D;
}

ul{
  list-style: none;
  /* border: 2px solid black; */
  padding: 1vmin;
  margin: 1vmin;
  /* max-height: 66vh ; */
  /* overflow-y : auto */
}
ul::-webkit-scrollbar {
  display: none;
}
li{
  display: flex;
  justify-content: space-between;
  background-color: #f8f9faab;
  color: #09293D;
  margin: 1vmin;
  padding: 0 2vmin;
  border-radius: 20px;
}

.buttonX {
  position: absolute;
  top: 0;
  right: 0;
  width: 10%;
  background-color: rgba(227, 43, 43, 0.982);
  padding: 0;

}

.submit-btn {
  position: fixed;
  bottom: 30px;
  width: 30%;
  color: #F8F9FA;
  background-color: rgba(227, 43, 43, 0.982);
}
.message{
  display: block;
  
 font-size: 5vmin;
  position: fixed;
  top:80px;
  color: #c30b08;
  right: 20%; 
font-weight: 500;
  

}
/* #dc5d5d  #44d468*/
/* /////////////SIDEBAR///////////// */
#userImg {
  margin-top: 25%;
  height: 20vmin;
  width: 20vmin;
  margin-bottom: 2vmin;

  /* border: 2px solid red; */
}

#sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;

}

.modal {
  background-color: var(--green);
  min-height: 90%;
  width: 45%;
  margin: auto;
  border-radius: 10px;


  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.699);
  /* Adds a shadow for the 3D effect */

  transition: all 0.5s ease;

}

.modal-overlay {
  background-color: rgba(0, 0, 0, 0.667);
  /* Semi-transparent black */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

}

.income-add {
 
  margin-top: 6vmin;
  border-top: 3px solid white;
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 30px;


}

.income-add input,
select {
  margin: 5px;
  padding: 5px;
  font-size: 3vmin;
  font-weight: 300;
  width: 70%;
  border-radius: 5px;
  border: none;

}

.income-add label {
  margin-left: 10px;
  width: 70%;
  font-size: 4vmin;
  font-weight: 300;
}



/* //////TOPBAR//////// */
.top-box{
  background-color: var(--green);
  padding: 3vmin;
  border-radius: 30px;
  width: 25%;
  color: #09293D;
  box-shadow: inset 8px 8px 16px 0 rgba(0, 0, 0, 0.2),
  inset -8px -8px 16px 0 rgba(255, 255, 255, 0.4);
}

.element {
  align-items: center;
  background-color: var(--green);
  box-shadow: inset 8px 8px 16px 0 rgba(0, 0, 0, 0.2),
  inset -8px -8px 16px 0 rgba(177, 191, 183, 0.4);
  border-radius: 50px;
  display: flex;
  height: 200px;
  justify-content: center;
  margin-right: 4rem;
  width: 200px;
}

/* /////////RESPONSIVE//////// */

@media (max-width: 500px) {
  .container {
    
    grid-template-columns: 1fr 3fr;
    grid-template-areas:
    "sidebar sidebar "
    "topbar topbar"
    "content1 content1"
    "content2 content2";

  }
  #sidebar {
    display: flex;
   
    align-items: center;  
  }
  .buttons{
    display: flex;
  }
  #userImg{
    margin: 0;
  }
  .modal{
    width: 100vw;
    height: 70vh;
 
  }
  .income-add{
    width: 100vw;
    height: 60vh;
    margin: 0;
    padding: 0;
    justify-content: space-between;
    margin-top: 60px;
  }
  .income-add label{
  
    font-size: 9vmin;
  }
  .income-add input,
  select{
    font-size: 7vmin;
  }
  .title{
    font-size: 9vmin;
    font-weight: 300;
  }
  .modal button{
    font-size: 8vmin;
    padding: 2vmin;
  }
  
}